.App {
  background-color: #162039;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  text-align: center;
}
.menu{
  border: 5px solid brown;
}
.menu input{
  max-width: 5vw;
}
.inputrow{
  display: inline-flex;
  text-align: left;
  margin: 5px;
}
.reset{
  margin-bottom: 5px;
}
.cell{
  display: inline-flex;
  box-sizing: border-box;
  border: 1px solid grey;
  height: 35px;
  width: 35px;
  text-align: center;
  background-color: white;
}
.zero{
  border-color: green;
  border-style: dotted;
  background-color: #006a4e;
}
.bombnear{
  background-color: rgb(92, 160, 142);
}
.cell:hover{
  cursor: pointer;
}
.gridWrapper{
  max-width: 100vw;
  border: 5px solid black;
}
